import React from "react";
import {
  GallerySectionContainer,
  CardWrapper,
  ImgWrap,
  Img,
  ImagesContainer,
} from "./GalleryElements";

import img1 from "../../images/wedding1.jpg";
import img2 from "../../images/wedding2.jpg";
import img3 from "../../images/wedding3.jpg";
import img4 from "../../images/wedding4.jpg";
import img5 from "../../images/wedding5.jpg";
import img6 from "../../images/wedding6.jpg";

const GallerySection = () => {
  return (
    <GallerySectionContainer id="gallery">
      <h1>גלריה</h1>
      <ImagesContainer>
        <CardWrapper>
          <ImgWrap>
            <Img src={img1} />
          </ImgWrap>
        </CardWrapper>
        <CardWrapper>
          <ImgWrap>
            <Img src={img2} />
          </ImgWrap>
        </CardWrapper>
        <CardWrapper>
          <ImgWrap>
            <Img src={img3} />
          </ImgWrap>
        </CardWrapper>
        <CardWrapper>
          <ImgWrap>
            <Img src={img4} />
          </ImgWrap>
        </CardWrapper>
        <CardWrapper>
          <ImgWrap>
            <Img src={img5} />
          </ImgWrap>
        </CardWrapper>
        <CardWrapper>
          <ImgWrap>
            <Img src={img6} />
          </ImgWrap>
        </CardWrapper>
      </ImagesContainer>
    </GallerySectionContainer>
  );
};

export default GallerySection;
